import { GET_PRODUCTS_URL, GET_PRODUCT_COUNT, GET_PRODUCT_VIEW_URL, } from "../consts";
import { HTTPMethods, } from "./types";
import { createEndpoint } from "./utils";
export function getProducts(data) {
    return createEndpoint(GET_PRODUCTS_URL, data, HTTPMethods.POST);
}
export function getProduct(id) {
    return createEndpoint(GET_PRODUCT_VIEW_URL(id), {}, HTTPMethods.GET);
}
export function getProductCount() {
    return createEndpoint(GET_PRODUCT_COUNT, {}, HTTPMethods.GET);
}
