import { writable } from 'svelte/store';
export function useSelected(arr = []) {
    const { subscribe, set, update } = writable(arr);
    return {
        subscribe,
        add: (name) => update((arr) => (arr = [...arr, name])),
        remove: (name) => update((arr) => (arr = arr.filter((x) => x !== name)))
    };
}
