import axios from 'axios';
import { API_URL } from '../consts';
import { HTTPMethods } from './types';
export async function createEndpoint(url, data, method, headers = {}) {
    let res = new Promise(() => undefined);
    url = API_URL + url;
    if (method === HTTPMethods.GET) {
        const req = await axios.get(url, data);
        res = (await req.data);
    }
    else if (method === HTTPMethods.POST) {
        const req = await axios.post(url, data, {
            headers: Object.assign({}, headers)
        });
        res = (await req.data);
    }
    else if (method === HTTPMethods.DELETE) {
        const req = await axios.delete(url, data);
        res = (await req.data);
    }
    return res;
}
export function handleError(e) {
    function isErrorResponse(e) {
        return e.response.data.detail !== undefined;
    }
    if (isErrorResponse(e)) {
        const err = e.response.data.detail;
        return {
            detail: err
        };
    }
    return {
        detail: 'Something went wrong.'
    };
}
