import { createDefaultCartStore } from '../../utils/defaultCreates';
import { writable } from 'svelte/store';
export const cart = writable({
    items: []
});
function _get_store(store) {
    let $val;
    store.subscribe(($) => ($val = $))();
    return $val;
}
export function addToCart(product, quantity) {
    cart.update((data) => {
        const _data = { items: [...data.items, { product, quantity }] };
        updateCartOnLocalStorage(_data);
        return _data;
    });
}
export function removeFromCart(id) {
    cart.update((data) => {
        const filtered_items = data.items.filter((x) => x.product.id !== id);
        const _data = { items: filtered_items };
        updateCartOnLocalStorage(_data);
        return _data;
    });
}
// ==================
// Public helper functions
// ==================
export function isInCart(id) {
    return _get_store(cart).items.filter((item) => item.product.id === id).length > 0;
}
export function calculateTotalCosts() {
    let total = 0;
    _get_store(cart).items.forEach((item) => (total += item.product.price * item.quantity));
    return total;
}
export function calculateTotalItems() {
    let total = 0;
    _get_store(cart).items.forEach((item) => (total += item.quantity));
    return total;
}
// ==================
// Private helper functions
// ==================
function updateCartOnLocalStorage(data) {
    localStorage.setItem('cart', JSON.stringify(data));
}
export function getCartOnLocalStorage() {
    if (localStorage.getItem('cart'))
        return JSON.parse(localStorage.getItem('cart'));
    return createDefaultCartStore();
}
